var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"general call"},[_c('section',{staticClass:"title"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"col-12"},[_c('h1',{staticClass:"documentTitle"},[_vm._v("Keynote Speakers")])])])]),_c('section',{staticClass:"body"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-3 text-center"},[_c('img',{staticClass:"rounded-circle img-fluid mb-5",attrs:{"src":require("@/assets/keynotes/neil.jpg")}})]),_c('div',{staticClass:"col-md-9"},[_c('h2',[_vm._v("Neil Heffernan")]),_c('p',[_vm._v(" Since 2002 "),_c('a',{attrs:{"href":"https://www.neilheffernan.net/","target":"blank"}},[_vm._v("Neil Heffernan")]),_vm._v(" has been a Professor at Worcester Polytechnic Institute in the Computer Science Department. He taught middle school math and science in the Teach for America program in Baltimore where he met his soon to be spouse, Cristina. When he got into CMU's PhD program in computer science he knew he would be building educational technology to help school teachers. Cristina and Neil created the ASSISTments, a platform used by 50,000 across the United States for daily classwork and nightly homework. In 1997 he had a seizure and was told he had brain cancer and only had 2-3 years to live; this helped motivate giving away this platform as a free public service. In October of 2016 an objective independent research organization, SRI International, "),_c('a',{attrs:{"href":"http://www.aboutus.assistments.org/homework-immediate-feedback---1-year-study.php","target":"_blank"}},[_vm._v("published a peer-review journal article")]),_vm._v(" reporting that ASSISTments caused students to learn 75% more on a standardized test of math achievement, compared to what they would have learned in a typical school year. In December of 2016 Cristina and Neil were invited to present at the White House on ASSISTments. Neil and Cristina now are funding by NSF and Schmidt Future to let other researchers use his platform and student to run their own studies. Neil has written 80+ papers on learner analytics (using data to predict some outcome in education) and over two dozen papers reporting the results on randomized controlled trials. ")]),_c('h3',[_vm._v("\"How Can Platforms Like ASSISTments Be Used to Improve Research?\" ")]),_c('p',[_vm._v(" The head of the Institute of Education Sciences "),_c('a',{attrs:{"href":"https://www.the74million.org/article/schneider-garg-medical-researchers-find-cures-by-conducting-many-studies-and-failing-fast-we-need-to-do-the-same-for-education/"}},[_vm._v("is asking about how to use platform")]),_vm._v(" to increase education sciences. "),_c('a',{attrs:{"href":"https://www.the74million.org/article/heffernan-how-can-we-know-if-ed-tech-works-by-encouraging-companies-researchers-to-share-data-government-funding-can-help/"}},[_vm._v("We have been addressing this")]),_vm._v(". So how do you use platforms like EdX, Khan Academy, Canvas to improve science? There is a crisis in American Science referred to as the Reproducibility Crisis where many experimental results are not able to be reproduced. We are trying to address this crisis by helping “good science” to be done. People that control platforms have a responsibility to try to make them useful tools for learning what works. In Silicon Valley, every company is doing AB Testing to refine their individual products. That, in and of itself, is a good thing and we should use these platforms to figure out how to make them more effective. One of the ways we should do that is by experimenting with different ways of helping students succeed. "),_c('a',{attrs:{"href":"http://www.assistments.org/"}},[_vm._v("ASSISTments")]),_vm._v(", a platform I have created, with 50,000 middle-school math students, is used to help scientists run studies. I will explain how we have over 100 experiments running inside the ASSISTments platform and how the ASSISTmentsTestBed.org allows external researchers to propose studies. I will also explain how proper oversight is done by our Institutional Review Board. Further, I will explain how users of this platform agree ahead of time to OpenScience procedures such as open-data, open-materials and pre-registration. I’ll illustrate some examples with the "),_c('a',{attrs:{"href":"https://www.etrialstestbed.org/"}},[_vm._v("twenty-four randomized controlled trials")]),_vm._v(" that I have published as well as the three studies that have more recently come out from the platform by others. Finally, I will point to how we are anonymizing our data and how over "),_c('a',{attrs:{"href":"https://sites.google.com/site/assistmentsstudies/useourdata"}},[_vm._v("34 different external researchers have used our datasets to publish scientific studies")]),_vm._v(". I would like to thank the U.S. Department of Education and the National Science Foundation for their support of over $32 million from 40+ grants. I will also address how COVID19 has driven a ten-fold increase in the number of teachers creating new ASSISTments accounts, and I will give my own personal take on how COVID19 highlights the need to keep teachers in the loop so that their students know their teachers are paying attention to their work and what it means for the AIED community. ")])])]),_c('hr'),_c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-md-3 text-center"},[_c('img',{staticClass:"rounded-circle img-fluid mb-5",attrs:{"src":require("@/assets/keynotes/yvonne.jpg")}})]),_c('div',{staticClass:"col-md-9"},[_c('h2',[_vm._v("Yvonne Rogers")]),_c('p',[_vm._v(" Yvonne Rogers is a Professor of Interaction Design, the director of UCLIC and a deputy head of the Computer Science department at University College London. Her research interests are in the areas of ubiquitous computing, interaction design and human-computer interaction. A central theme of her work is concerned with designing interactive technologies that augment humans. A current focus of her research is on human data interaction. She is also interested in what human-centered AI means in practice. Central to her work is a critical stance towards how visions, theories and frameworks shape the fields of HCI, cognitive science and Ubicomp. She has been instrumental in promulgating new theories (e.g., external cognition), alternative methodologies (e.g., in the wild studies) and far-reaching research agendas (e.g., "),_c('a',{attrs:{"href":"http://research.microsoft.com/en-us/um/cambridge/projects/hci2020/"}},[_vm._v("\"Being Human: HCI in 2020\"")]),_vm._v("). She has also published two monographs "),_c('a',{attrs:{"href":"http://www.morganclaypool.com/doi/abs/10.2200/S00418ED1V01Y201205HCI014"}},[_vm._v("\"HCI Theory: Classical, Modern and Contemporary.\"")]),_vm._v(" and "),_c('a',{attrs:{"href":"http://www.morganclaypool.com/doi/abs/10.2200/S00764ED1V01Y201703HCI037"}},[_vm._v("\"Research in the Wild.\"")]),_vm._v(" with Paul Marshall. She is a fellow of the ACM, BCS and the ACM CHI Academy. ")]),_c('h3',[_vm._v("\"The New Zeitgeist: Human-AI\"")]),_c('p',[_vm._v(" In place of the Singularity, Superintelligence and General AI visions that have dominated much of the debate surrounding AI (that predicted that machines will become eventually become more intelligent than human beings and take over the world) quite different ways of imagining AI are now emerging that are less dystopian or utopian-driven. In contrast, a new discourse is emerging that is rethinking the benefits of future AI advances from a more human perspective. The main thrust of this approach is to orient towards envisioning new forms of human-AI partnerships, where humans collaborate with, talk to, or even confide in AI, and conversely, where AI, through its various guises, becomes a companion, therapist, colleague, assistant or other. Such a shift in thinking enables researchers and developers to design quite different kinds of intelligent systems - those that augment humans. The implications of doing so are profound; especially when considering how to enhance the way learners, educators and teachers can collaborate with AI in the future. In my talk I will begin to describe what the opportunities and challenges are with this new framing for AI and Ed. ")])])]),_c('hr'),_c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-md-3 text-center"},[_c('img',{staticClass:"rounded-circle img-fluid mb-5",attrs:{"src":require("@/assets/keynotes/andreas.jpg")}})]),_c('div',{staticClass:"col-md-9"},[_c('h2',[_vm._v("Andreas Schleicher")]),_c('p',[_vm._v(" Andreas Schleicher is Director for Education and Skills, and Special Advisor on Education Policy to the Secretary-General at the Organisation for Economic Co-operation and Development (OECD). He initiated and oversees the Programme for International Student Assessment (PISA) and other international instruments, like the OECD Teaching and Learning International Survey (TALIS) and the Survey of Adult Skills (PIAAC), which create a global platform for policy-makers, researchers and educators to innovate and transform educational policies and practices. He has worked for over 20 years with Ministers and educational leaders around the world to improve quality and equity in education. U.S. Education Secretary Arne Duncan said about Andreas in The Atlantic (7/2011) that “He understands the global issues and challenges as well as or better than anyone I’ve met, and he tells me the truth”. Secretary of State Michael Gove in the United Kingdom called Andreas “the most important man in English education”, never mind that he is German and lives in France. Andreas is the recipient of numerous honours and awards, including the “Theodor Heuss” prize, awarded for “exemplary democratic engagement” in the name of the first president of the Federal Republic of Germany. He holds an honorary professorship at the University of Heidelberg. ")])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-3 text-center"},[_c('img',{staticClass:"rounded-circle img-fluid mb-5",attrs:{"src":require("@/assets/keynotes/jim.jpg")}})]),_c('div',{staticClass:"col-md-9"},[_c('h2',[_vm._v("Jim Knight")]),_c('p',[_vm._v(" Jim Knight works in education, medtech and as a legislator. He is an expert in politics, leadership, digital disruption, and talent recruitment and development."),_c('br'),_vm._v(" Jim is the Chief Education and External Officer at TES Global Ltd, the news, training, jobs and resource sharing service for teachers. His responsibilities include all corporate communications and Tes Institute, a teacher training business he founded and that is now the fifth largest qualifier of teachers in the UK. Tes Institute uses online learning extensively for high quality post graduate level learning, delivered as professional development."),_c('br'),_vm._v(" Jim is also a founder of XRapid, enabling diagnosis of malaria and asbestos fibres using iPhones. This technology used machine learning and pattern recognition to replace monotonous laboratory practice."),_c('br'),_vm._v(" As a UK government minister his portfolios included rural affairs, schools, digital and employment. He was a member of Gordon Brown’s Cabinet 2009-2010, before joining the Lords after the 2010 General Election. He is the only person to have won and retained a seat for Labour in a General Election in Dorset. He is also a visiting Professor at the Knowledge Lab of the UCL Institute of Education and Chair of Whole Education. ")]),_c('h3',[_vm._v("\"A fireside chat about how AI impacts upon the policy making landscape in education, particularly in the wake of the current pandemic.\"")]),_c('p')])]),_c('hr')])])])}]

